.Home {
  text-align: center;
}

.Home-logo {
  height: 40vmin;
  pointer-events: none;
}

.Home-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  position: relative;
}


.StreamButton, .ListenButton{
  position: absolute;
}

.StreamButton{
  left: 50%;
  margin-left: -250px;
}

.ListenButton{
  right: 50%;
  margin-right: -250px;
}





